  /* Style the buttons inside the tab */
  .tab button, .tab-bottom button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 6px 16px;
    transition: 0.3s;
    font-size: 17px;
    border-radius: 7px;
    background-image: linear-gradient(to right, #0069f5 , #00409a);
    margin: 0 6px;
}
  
  /* Create an active/current tablink class */
  .tab button.active, .tab-bottom button.active {
    border: 2px solid #0063e8;
    background: #12193c;
}
  /* Style the tab content */
  .tabcontent {
    padding: 6px 12px;
    border-top: none;
    margin-top: 70px;
}

