/* tooltip  */
.tooltip-area {
    text-align: right;
}
.tooltip {
    position: absolute;
    display: inline-block;
    margin-left: 13px;
}
.tooltip .tooltiptext {
    visibility: hidden;
    width: 210px;
    color: #fff;
    border-radius: 6px;
    padding: 2px 5px;
    position: absolute;
    z-index: 1;
    bottom: 107%;
    right: 27%;
    margin-left: -60px;
    text-align: center;
    background-image: linear-gradient(to right, #262d51 , #101634);
    border: 2px solid #65697e;
    font-size: 14px;
}
.tooltip .tooltiptext a {
    color: #0899e7;
    font-weight: 600;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}
.tooltip i {
    text-align: left;
    border: 1px solid #145b8e;
    padding: 5px 11px;
    /* margin: 4px 2px 6px 0; */
    border-radius: 50%;
    font-size: 12px;
}
.tooltip-area {
    text-align: right;
    margin-top: 2px;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 4px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(185, 184, 184); 
    border-radius: 3px;
    
  }

