.checkbox {
	opacity: 0;
	position: absolute;
}
.label {
    background-color: #111;
    border-radius: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    position: relative;
    height: 15px;
    width: 43px;
    transform: scale(1.5);
    left: 14px;
}
.label .ball {
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0px;
    left: 2px;
    height: 15px;
    width: 15px;
    transform: translateX(22px);
    transition: transform 0.2s linear;
}

.checkbox:checked + .label .ball {
	transform: translateX(0px);
}
